import React, { useState, useRef } from 'react';
import Header from './components/Header';
import Hero from './components/Hero';
import About from './components/About';
import Camps from './components/Camps';
import Apply from './components/Apply';
import Footer from './components/Footer';
import './App.css';

function App() {
  const [selectedInfo, setSelectedInfo] = useState({ camp: null, schedule: null });
  const applyRef = useRef(null);

  const handleCampSelect = (camp, schedule) => {
    setSelectedInfo({ camp, schedule });
    setTimeout(() => {
      applyRef.current?.scrollIntoView({ behavior: 'smooth' });
    }, 100);
  };

  return (
    <div className="App">
      <Header />
      <main>
        <Hero />
        <About />
        <Camps onCampSelect={handleCampSelect} />
        <Apply selectedInfo={selectedInfo} ref={applyRef} />
      </main>
      <Footer />
    </div>
  );
}

export default App;
