import React, { useState, forwardRef } from 'react';
import { Container, Row, Col, Form, Card, Alert } from 'react-bootstrap';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import PillButton from './PillButton';

const Section = styled.section`
  padding: 80px 0;
  background-color: var(--secondary-color);
`;

const ApplyCard = styled(motion.div)`
  background: var(--glass-bg);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 100px;
  border: var(--glass-border);
  padding: 2.5rem;

  @media (max-width: 768px) {
    padding: 2rem 1.5rem;
    border-radius: 50px;
  }
`;

const StyledFormControl = styled(Form.Control)`
  background-color: rgba(255, 255, 255, 0.05) !important;
  color: var(--text-color) !important;
  border: 1px solid var(--glass-border) !important;
  border-radius: 20px !important;
  box-shadow: none !important;
  padding: 0.75rem 1.25rem !important;

  &:focus {
    border-color: var(--accent-color-1) !important;
  }

  &::placeholder {
    color: var(--text-color-darker) !important;
    opacity: 1;
  }

  &:disabled {
    background-color: rgba(0,0,0,0.2) !important;
    opacity: 0.7;
  }
`;

const StyledFormLabel = styled(Form.Label)`
  color: var(--text-color-darker) !important;
  margin-bottom: 0.5rem;
  display: block;
`;

const StyledFormCheck = styled(Form.Check)`
  .form-check-input {
    background-color: rgba(255, 255, 255, 0.05);
    border-color: var(--glass-border);
  }
  .form-check-input:checked {
    background-color: var(--accent-color-1);
    border-color: var(--accent-color-1);
  }
  .form-check-label {
    color: var(--text-color);
  }
`;

const Apply = forwardRef(({ selectedInfo }, ref) => {
  const { camp, schedule } = selectedInfo;
  
  // Form state
  const [validated, setValidated] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [formData, setFormData] = useState({
    parentName: '',
    parentContact: '',
    childName: '',
    childGender: '',
    childSchool: '',
    childInterests: '',
    source: '',
    sourceOther: '',
    email: ''
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };
  
  const handleRadioChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  }

  const handleSubmit = (event) => {
    const form = event.currentTarget;
    event.preventDefault();

    if (form.checkValidity() === false) {
      event.stopPropagation();
      setValidated(true);
      return;
    }

    setValidated(true);
    setIsSubmitting(true);

    const scriptURL = 'https://script.google.com/macros/s/AKfycbzighPRKaf_go-BGxjlX9Rxb8YKZg8JjZW32q71AttLp5r30I6aaQPz0orb_hDGYjkVgA/exec';
    const data = new FormData();
    
    data.append('camp_title', camp.title);
    data.append('schedule_details', schedule.name);
    data.append('parent_name', formData.parentName);
    data.append('parent_phone', formData.parentContact);
    data.append('child_name', formData.childName);
    data.append('child_gender', formData.childGender);
    data.append('child_school_age', formData.childSchool);
    data.append('child_interests', formData.childInterests);
    data.append('referral_source', formData.source);
    data.append('referral_other', formData.sourceOther);
    data.append('email', formData.email);

    fetch(scriptURL, { method: 'POST', body: data})
      .then(response => {
        setShowAlert(true);
        setIsSubmitting(false);
        console.log('Success!', response);
      })
      .catch(error => {
        alert('  .  .');
        setIsSubmitting(false);
        console.error('Error!', error.message);
      });
  };

  return (
    <Section id="apply" ref={ref}>
      <Container>
        <Row className="justify-content-center">
          <Col md={8}>
            <div className="text-center mb-5">
              <h2 className="gradient-text">Join a Program</h2>
              <p className="lead text-white-50">     .</p>
            </div>
            
            <ApplyCard initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ duration: 0.5 }}>
              {camp && schedule ? (
                <Form noValidate validated={validated} onSubmit={handleSubmit}>
                  <StyledFormLabel> </StyledFormLabel>
                  <StyledFormControl disabled value={`${camp.title} - ${schedule.name}`} className="mb-3" />

                  <Row>
                    <Col md={6} className="mb-3">
                      <StyledFormLabel> </StyledFormLabel>
                      <StyledFormControl required name="parentName" value={formData.parentName} onChange={handleInputChange} type="text" placeholder=": " />
                    </Col>
                    <Col md={6} className="mb-3">
                      <StyledFormLabel> </StyledFormLabel>
                      <StyledFormControl required name="parentContact" value={formData.parentContact} onChange={handleInputChange} type="tel" placeholder="010-1234-5678" />
                    </Col>
                  </Row>

                  <Row>
                    <Col md={6} className="mb-3">
                      <StyledFormLabel> </StyledFormLabel>
                      <StyledFormControl required name="childName" value={formData.childName} onChange={handleInputChange} type="text" placeholder=": " />
                    </Col>
                    <Col md={6} className="mb-3">
                      <StyledFormLabel> </StyledFormLabel>
                      <div>
                        <StyledFormCheck inline label="" name="childGender" type="radio" id="male" value="" onChange={handleRadioChange} required />
                        <StyledFormCheck inline label="" name="childGender" type="radio" id="female" value="" onChange={handleRadioChange} required />
                      </div>
                    </Col>
                  </Row>

                  <StyledFormLabel>   </StyledFormLabel>
                  <StyledFormControl required name="childSchool" value={formData.childSchool} onChange={handleInputChange} type="text" placeholder=":  4" className="mb-3" />

                  <StyledFormLabel> </StyledFormLabel>
                  <StyledFormControl as="textarea" name="childInterests" value={formData.childInterests} onChange={handleInputChange} placeholder=": , ,   " className="mb-3" />
                  
                  <StyledFormLabel>  ?</StyledFormLabel>
                  <div className="mb-3">
                    {['SNS', '', '', ''].map((type) => (
                      <StyledFormCheck 
                        inline 
                        label={type} 
                        name="source" 
                        type="radio" 
                        id={`source-${type}`} 
                        key={type}
                        value={type}
                        onChange={handleRadioChange}
                        checked={formData.source === type}
                      />
                    ))}
                  </div>

                  {formData.source === '' && (
                    <StyledFormControl
                      name="sourceOther"
                      value={formData.sourceOther}
                      onChange={handleInputChange}
                      type="text"
                      placeholder="  ."
                      className="mb-3"
                    />
                  )}

                  <StyledFormLabel></StyledFormLabel>
                  <StyledFormControl required name="email" value={formData.email} onChange={handleInputChange} type="email" placeholder="   " className="mb-3" />

                  <div className="text-center mt-4">
                    <PillButton type="submit" disabled={isSubmitting}>
                      {isSubmitting ? ' ...' : ' '}
                    </PillButton>
                  </div>
                  
                  {showAlert && (
                    <Alert variant="success" onClose={() => setShowAlert(false)} dismissible className="mt-4">
                      <Alert.Heading as="h5">  !</Alert.Heading>
                      <p className="mb-0">
                         <a href="https://pf.kakao.com/_DZhmn" target="_blank" rel="noopener noreferrer"> </a> .
                      </p>
                    </Alert>
                  )}
                </Form>
              ) : (
                <div className="text-center text-white-50 py-5">
                  <p className="h5">     .</p>
                  <p>  ''  !</p>
                </div>
              )}
            </ApplyCard>
          </Col>
        </Row>
      </Container>
    </Section>
  );
});

export default Apply;
