import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { campsData } from '../data';
import PillButton from './PillButton';

const Section = styled.section`
  padding: 5rem 0;
  background-color: var(--primary-color);
`;

const CampCard = styled(motion.div)`
  background: var(--glass-bg);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 25px;
  border: 1px solid var(--glass-border);
  padding: 2.5rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-10px);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    border-color: var(--accent-color-1);
  }

  h4, h5, h4 + p {
    color: var(--text-color);
  }

  @media (max-width: 768px) {
    padding: 2rem;
  }
`;

const CardBody = styled.div`
  flex-grow: 1;
  text-align: left;
`;

const ScheduleBox = styled.div`
  background-color: rgba(0,0,0,0.2);
  border-radius: 15px;
  padding: 1rem 1.5rem;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  strong {
    color: var(--text-color);
  }
`;

const ApplyButton = styled(PillButton)`
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
`;

const NotionButton = styled(PillButton)`
  margin-top: 7.5rem;
`;;

const cardVariants = {
  offscreen: { y: 50, opacity: 0 },
  onscreen: { y: 0, opacity: 1, transition: { type: "spring", bounce: 0.4, duration: 0.8 } }
};

const Camps = ({ onCampSelect }) => {
  return (
    <Section id="camps">
      <Container>
        <Row className="text-center mb-5">
          <Col>
            <h2 className="gradient-text">Our Core Programs</h2>
            <p className="lead text-white-50">       .</p>
            <NotionButton 
              href="https://remarkable-staircase-dc5.notion.site/AI-1_-23028eaed3db81b6bf7ec5dcb3467514" 
              target="_blank" 
              rel="noopener noreferrer"
              as="a"
            >
                 (Notion)
            </NotionButton>
          </Col>
        </Row>
        <Row>
          {campsData.map(camp => (
            <Col lg={6} className="mb-4" key={camp.id}>
              <CampCard initial="offscreen" whileInView="onscreen" viewport={{ once: true, amount: 0.2 }} variants={cardVariants}>
                <CardBody>
                  <h4>{camp.title}</h4>
                  <p><strong>:</strong> {camp.target}</p>
                  <p className="text-white-50">{camp.description}</p>
                  <p className="text-warning">{camp.price}</p>
                  <hr style={{ borderColor: "rgba(255, 255, 255, 0.3)" }} />
                  <h5 className="mt-4 mb-3"> </h5>
                  {camp.schedules && camp.schedules.length > 0 ? (
                    <div>
                      {camp.schedules.map(schedule => (
                        <ScheduleBox key={schedule.id}>
                          <div>
                            <strong>{schedule.name}</strong>
                            <div className="small text-muted">{schedule.dates}</div>
                            <div className="small text-muted">{schedule.time}</div>
                          </div>
                          <ApplyButton 
                            onClick={() => onCampSelect(camp, schedule)}
                            disabled={schedule.dates === '' || schedule.dates === ' '}
                          >
                            {schedule.dates === '' ? '' : ''}
                          </ApplyButton>
                        </ScheduleBox>
                      ))}
                    </div>
                  ) : (
                    <p className="text-white-50">  .   .</p>
                  )}
                </CardBody>
              </CampCard>
            </Col>
          ))}
        </Row>
      </Container>
    </Section>
  );
};

export default Camps;